@charset "UTF-8";
/* Base */
/*---------------------------------------------------------*/
body {
  font-family: sans-serif;
}

.en {
  font-family: "Josefin Sans", sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

/* スクリーンリーダー用テキスト */
/* --------------------------------------------------------- */
.sr-only {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
}

/* Page Grid */
/* -------------------------------------------------------- */
body.page-grid {
  display: grid;
  grid-template-columns: 72px auto 72px;
  grid-template-rows: 72px auto auto;
}

/* Parts Grid */
/* -------------------------------------------------------- */
.parts-grid {
  display: grid;
  grid-template-columns: 20px auto 20px;
}
@media (min-width: 768px) {
  .parts-grid {
    grid-template-columns: minmax(72px, 1fr) minmax(auto, 1074px) minmax(72px, 1fr);
  }
}

.parts-grid > *,
.parts-grid::before,
.parts-grid::after {
  grid-column: 2;
}

/* Navigation Button */
/* -------------------------------------------------------- */
.nav-button {
  grid-column: 3;
  grid-row: 1;
  outline: none;
  border: none;
  background-color: #98a718;
  cursor: pointer;
  z-index: 200;
}
.nav-button::before, .nav-button::after {
  content: "";
  display: block;
  margin: auto;
  width: 32px;
  height: 4px;
  background-color: #ffffff;
  transition: transform 0.5s;
}
.nav-button::before {
  transform: translateY(-5px);
}
.nav-button::after {
  transform: translateY(5px);
}

/* overlay */
.nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 100;
  display: grid;
  justify-items: center;
  align-content: center;
  gap: 50px;
  transform: scale(0, 0);
  transform-origin: right top;
  transition: transform 0.5s;
}

.open html, .open body {
  /* Prevention of scroll display */
  height: 100%;
  overflow: hidden;
}
.open .nav {
  transform: scale(1, 1);
}
.open .nav-button::before {
  transform: translateY(2px) rotate(45deg);
}
.open .nav-button::after {
  transform: translateY(-2px) rotate(-45deg);
}

@media (pointer: fine) and (hover: hover) {
  html {
    overflow-y: scroll;
  }

  .open html, .open body {
    height: 100vh;
  }
}
/* SNS */
/* -------------------------------------------------------- */
.sns {
  grid-column: 3;
  grid-row: 2;
  justify-self: end;
  align-self: start;
  margin-top: 187px;
  margin-right: 10px;
  display: grid;
  gap: 30px;
  z-index: 10;
}
@media (min-width: 768px) {
  .sns {
    justify-self: center;
    margin-right: 0;
  }
}

/* Decoration */
/* -------------------------------------------------------- */
.decoration {
  grid-column: 1;
  grid-row: 2;
  justify-self: start;
  align-self: start;
  margin-top: 257px;
  margin-left: 10px;
  color: #aaaaaa;
  font-size: 20px;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  z-index: 10;
}
@media (min-width: 768px) {
  .decoration {
    justify-self: center;
    margin-left: 0;
  }
}

/* 丸の付いた直線 */
@media (min-width: 768px) {
  body.page-grid::before,
body.page-grid::after {
    content: "";
    display: block;
    grid-column: 1;
    grid-row: 2;
    justify-self: center;
    background-color: #98a718;
    margin-top: 660px;
    z-index: 10;
  }
}

.page-grid::before {
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.page-grid::after {
  width: 1px;
  min-height: 200px;
}

/* Footer */
/* -------------------------------------------------------- */
.footer.parts-grid {
  grid-column: 1/-1;
  grid-row: 3;
  background-color: #000000;
  color: #ffffff;
  padding: 81px 0 86px;
}
.footer.parts-grid > * {
  grid-row: 1;
}
.footer.parts-grid .text .site {
  font-size: 37px;
  font-weight: bold;
  margin-bottom: 35px;
}
.footer.parts-grid .text .address {
  font-size: 12px;
  line-height: 1.5;
}
.footer.parts-grid .chat {
  justify-self: end;
  margin-top: -100px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  color: #98a718;
}
@media (min-width: 768px) {
  .footer.parts-grid .chat {
    margin-top: 0;
  }
}
.footer.parts-grid .chat img {
  padding-bottom: 7px;
}

/* Main Contents */
/* -------------------------------------------------------- */
main {
  grid-column: 1/-1;
  grid-row: 2;
}

/* Head Image */
.head-image .site {
  margin-top: 33px;
  font-size: 37px;
  font-weight: bold;
  color: #98a718;
}
.head-image figure {
  grid-column: 1/-1;
}
@media (min-width: 768px) {
  .head-image figure {
    grid-column: 2;
  }
}
.head-image figure img {
  width: 100%;
  height: 500px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .head-image figure img {
    object-position: center bottom;
  }
}

/* Article */
.post {
  row-gap: 28px;
  padding: 50px 0 135px;
}
@media (min-width: 768px) {
  .post.parts-grid {
    grid-template-columns: minmax(72px, 1fr) minmax(auto, 860px) minmax(72px, 1fr);
    row-gap: 36px;
    padding: 125px 0 116px;
  }
}
.post h1 {
  font-size: 28px;
  line-height: 1.5;
  font-weight: bold;
  margin-bottom: 23px;
}
@media (min-width: 768px) {
  .post h1 {
    font-size: 48px;
    margin-bottom: 19px;
  }
}
.post p {
  font-size: 16px;
  line-height: 1.75;
}
@media (min-width: 768px) {
  .post p {
    font-size: 18px;
    line-height: 2;
  }
}

/* Gallery */
.gallery {
  display: grid;
  grid-template-columns: 158fr 167fr;
  grid-template-rows: 173px 136px 136px;
  grid-template-areas: "A A" "B C" "B D";
  gap: 10px;
  margin: 12px 0;
}
@media (min-width: 768px) {
  .gallery {
    grid-template-columns: 334fr 248fr 248fr;
    grid-template-rows: 278px 205px;
    grid-template-areas: "B A A" "B C D";
    gap: 15px;
    margin: 25px 0;
  }
}
.gallery .photo-A {
  grid-area: A;
}
.gallery .photo-B {
  grid-area: B;
}
.gallery .photo-C {
  grid-area: C;
}
.gallery .photo-D {
  grid-area: D;
}
.gallery img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Main Index*/
/* -------------------------------------------------------- */
/* Hero Header */
.hero {
  background-color: #ffffff;
}
.hero > * {
  grid-column: 1/-1;
  grid-row: 1;
  justify-self: center;
}
.hero h1, .hero p {
  line-height: 1;
  color: #ffffff;
  mix-blend-mode: difference;
  z-index: 2;
}
.hero h1 {
  font-size: 91px;
  font-weight: bold;
  margin-top: 30px;
}
@media (max-width: 374px) {
  .hero h1 {
    font-size: 75px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .hero h1 {
    font-size: 160px;
  }
}
@media (min-width: 1023px) {
  .hero h1 {
    font-size: 229px;
    margin-top: 33px;
  }
}
.hero p {
  font-size: 17px;
  margin-top: 120px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .hero p {
    font-size: 30.5px;
    margin-top: 185px;
  }
}
@media (min-width: 1023px) {
  .hero p {
    font-size: 44px;
    margin-top: 251px;
  }
}
.hero figure img {
  width: 302px;
  height: 506px;
  object-fit: cover;
  margin: 63px 0 100px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .hero figure img {
    width: 395px;
    height: 572.5px;
  }
}
@media (min-width: 1023px) {
  .hero figure img {
    width: 488px;
    height: 639px;
    margin: 84px 0 183px;
  }
}

/* Message */
.msg {
  row-gap: 51px;
  padding: 64px 0;
}
@media (min-width: 768px) {
  .msg {
    padding: 150px 0;
  }
}
.msg::before {
  content: "";
  display: block;
  width: 26px;
  height: 27px;
  background-color: #98a718;
  grid-row: 1;
  z-index: 2;
}
.msg.msg-black {
  color: #ffffff;
  background-color: #000000;
}
.msg.msg-black img {
  object-position: left bottom;
}
.msg.msg-white::before {
  justify-self: end;
}
.msg.msg-white::after {
  content: "";
  display: block;
  height: 130px;
  z-index: -1;
  background-color: #98a718;
  opacity: 60%;
  grid-column: 1;
  grid-row: 1;
  align-self: end;
  margin-bottom: -25px;
  margin-right: -110px;
}
@media (min-width: 768px) {
  .msg.msg-white::after {
    height: 256px;
    margin-bottom: -150px;
    margin-right: -113px;
  }
}
.msg figure {
  grid-row: 1;
}
@media (min-width: 768px) {
  .msg figure {
    grid-row: 1;
    width: 44%;
  }
}
.msg img {
  width: 100%;
  height: 288px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .msg img {
    height: 100%;
  }
}
@media (min-width: 768px) {
  .msg .text {
    grid-row: 1;
    width: 44%;
    margin: 89px 0 49px;
  }
}
.msg .text h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 14px;
}
@media (min-width: 768px) {
  .msg .text h2 {
    font-size: 36px;
  }
}
.msg .text .text-subtitle {
  font-size: 20px;
  color: #aaaaaa;
  margin-bottom: 40px;
}
.msg .text .text-body {
  font-size: 16px;
  line-height: 1.8;
  margin-bottom: 47px;
}
.msg .text .text-link {
  font-size: 16px;
  font-weight: bold;
  color: #98a718;
}

@media (min-width: 768px) {
  .msg-black figure,
.msg-white .text {
    justify-self: end;
  }
}